import React from 'react'
import Link from 'gatsby-link'

import styled from "styled-components";

import { Menu, Icon, Button, Dropdown } from 'semantic-ui-react'

//import {Menu} from 'semantic-ui-react';

// const Header = ({ siteTitle }) => (
//   <Menu className="ui inverted menu">
//     <Menu.Menu>
//       <Menu.Item className="active red item">Tuna</Menu.Item>
//     </Menu.Menu>
//   </Menu>
// )
const options = [ { key: 'Arabic', text: 'Arabic', value: 'Arabic' }]
class Header extends React.Component {
  constructor(props){
    super(props);
    console.log("header");
    console.log(props);
    this.state = { activeItem: 'home' }
  }
  handleItemClick = (e, { name }) => this.setState({ activeItem: name })

  render() {
    const { activeItem } = this.state

    return (
      <Menu>
        <MyMenuBtn>
          <Menu.Item>
            <Link exact to="/" activeClassName="active">{this.props.siteTitle}</Link>
          </Menu.Item>
        </MyMenuBtn>
        <MyMenuBtn>
          <Menu.Item>
            <Link exact to="/games" activeClassName="active">Games</Link>
          </Menu.Item>
        </MyMenuBtn>
        <Menu.Menu position='right'>
          <Dropdown text='Shopping' pointing className='link item'>
              <Dropdown.Menu>
                    <Dropdown.Header>Mens</Dropdown.Header>
                    <Dropdown.Item>Shirts</Dropdown.Item>
                    <Dropdown.Item>Pants</Dropdown.Item>
                    <Dropdown.Divider />
                    <Dropdown.Header>Womens</Dropdown.Header>
                    <Dropdown.Item>Dresses</Dropdown.Item>
              </Dropdown.Menu>
          </Dropdown>
          </Menu.Menu>
    </Menu>
    )
  }
}


export default Header
const height = 50;
// const Menu = styled.div`
