const MyMenuBtn = styled.span`
  .active{
    color: red;
    
  }
`
const MenuRight = styled.span`
  float: right;
  display: 'inline'
  height: ${height}px;
  .active{
    border-bottom: 4px solid grey;
  }
`
const MenuLeft = styled.span`
  float: left;
  display: 'inline'
  background-color: azure;
  .active{
    border-bottom: 4px solid grey;
  }
`
const MenuIcon = styled.div`
  display: inline-block;
  height: ${height}px;
  padding-top: ${height/5}px;
`
const Title = styled.p`

  display: inline;
`
const styleHeading = {
  display: 'inline'
}

const styleLink={
  height: `${height}px`,
  color: 'grey',
  textDecoration: 'none',
  display: 'inline-block',
  verticalAlign: 'baseline',
  padding: `${height/5}px`,
  lineHeight: `${height}px`
}
